function fullRootRule(): RootRule {
  return {
    rules: {
      version: {
        description: 'The version of the dependabot file',
        required: true,
        values: ['2'],
      },
      'enable-beta-ecosystems': {
        description: 'Enable beta ecosystems',
        single: ['boolean'],
      },
      registries: {
        description: 'Private registries that Dependabot can access',
        map: {
          async validateKey(template: unknown, path: string[]): Promise<Validation | undefined> {
            if (typeof path[1] != 'string') {
              return {
                message: html`Registry id must be a string`,
                severity: 'error',
              }
            }
          },
          rules: {
            type: {
              description: 'The type of registry',
              values: [
                'composer-repository',
                'docker-registry',
                'git',
                'hex-repository',
                'hex-organization',
                'maven-repository',
                'npm-registry',
                'nuget-feed',
                'python-index',
                'rubygems-server',
                'terraform-registry',
              ],
            },
            url: {
              description: 'The URL of the registry',
              single: ['string'],
            },
            username: {
              description: 'The username to use to access the registry',
              single: ['string'],
            },
            password: {
              description: 'The password to use to access the registry',
              single: ['string'],
            },
            key: {
              description: 'The key to use to access the registry',
              single: ['string'],
            },
            token: {
              description: 'The token to use to access the registry',
              single: ['string'],
            },
            repo: {
              description: 'The name to use to identify the repository',
              single: ['string'],
            },
            'auth-key': {
              description: 'The auth key to use to access the registry',
              single: ['string'],
            },
            'public-key-fingerprint': {
              description: 'The public key fingerprint to use to verify the repository',
              single: ['string'],
            },
            'replaces-base': {
              description:
                'Resolves dependencies by using the specified URL rather than the base URL of the Python Package Index',
              single: ['boolean'],
            },
          },
        },
      },
      updates: {
        required: true,
        items: {
          rules: {
            allow: {
              description: 'Customize which updates are allowed',
              items: {
                values: ['dependency-name', 'dependency-type'],
                rules: {
                  'dependency-name': {
                    description: 'The name of the dependency',
                    single: ['string'],
                  },
                  'dependency-type': {
                    description: 'The type of dependency',
                    values: ['direct', 'indirect', 'all', 'production', 'development'],
                  },
                },
              },
            },
            assignees: {
              description: 'Assignees to assign to pull requests',
              multipleValues: true,
            },
            'commit-message': {
              description: 'Commit message preferences',
              rules: {
                prefix: {
                  description: 'Prefix to use for commit messages',
                  single: ['string'],
                  validateValue: async (
                    template: unknown,
                    path: string[],
                    value: unknown,
                  ): Promise<Validation | undefined> => {
                    if (typeof value != 'string') return
                    if (value.length > 50) {
                      return {
                        message: html`Commit message prefix must be 50 characters or less`,
                        severity: 'error',
                      }
                    }
                  },
                },
                'prefix-development': {
                  description: 'Prefix to use for commit messages for development dependencies',
                  single: ['string'],
                  validateValue: async (
                    template: unknown,
                    path: string[],
                    value: unknown,
                  ): Promise<Validation | undefined> => {
                    if (typeof value != 'string') return
                    if (value.length > 50) {
                      return {
                        message: html`Commit message prefix must be 50 characters or less`,
                        severity: 'error',
                      }
                    }
                  },
                },
                include: {
                  description:
                    'Specifies that any prefix is followed by a list of the dependencies updated in the commit',
                  values: ['scope'],
                },
              },
            },
            directory: {
              description: 'Location of the package manifests',
              required: true,
              single: ['string'],
            },
            'enable-beta-ecosystems': {
              description: 'Enable support for beta ecosystems',
              single: ['boolean'],
            },
            ignore: {
              description: 'Specify which dependencies are ignored',
              items: {
                values: ['dependency-name', 'update-types', 'versions'],
                rules: {
                  'dependency-name': {
                    description: 'Ignore updates for dependencies with matching names',
                    single: ['string'],
                  },
                  versions: {
                    description: 'Ignore updates for dependencies with matching versions',
                    items: {
                      single: ['string'],
                    },
                  },
                  'update-types': {
                    description: 'Ignore types of updates',
                    items: {
                      values: [
                        'version-update:semver-major',
                        'version-update:semver-minor',
                        'version-update:semver-patch',
                      ],
                    },
                  },
                },
              },
            },
            'insecure-external-code-execution': {
              description: 'Allow insecure external code execution',
              values: ['allow', 'deny'],
            },
            labels: {
              description: 'Labels to add to pull requests',
              multipleValues: true,
            },
            milestone: {
              description: 'Milestone to add to pull requests',
              single: ['number'],
            },
            'open-pull-requests-limit': {
              description: 'Limit the number of open pull requests',
              single: ['number'],
            },
            'package-ecosystem': {
              description: 'Package manager to use',
              values: [
                'bundler',
                'cargo',
                'composer',
                'docker',
                'elm',
                'github-actions',
                'gitsubmodule',
                'gomod',
                'gradle',
                'maven',
                'mix',
                'npm',
                'nuget',
                'pip',
                'pub',
                'terraform',
              ],
              required: true,
            },
            'pull-request-branch-name': {
              rules: {
                separator: {
                  description: 'Separator to use between the package manager and the dependency',
                  values: ['-', '/', '_'],
                },
              },
            },
            registries: {
              description: 'Private registries that Dependabot can access, as specified above',
              multipleValues: true,
            },
            'rebase-strategy': {
              description: 'How to rebase pull requests',
              values: ['disabled', 'auto'],
            },
            reviewers: {
              description: 'Reviewers to assign to pull requests',
              multipleValues: true,
            },
            schedule: {
              values: ['interval', 'day', 'time', 'timezone'],
              required: true,
              rules: {
                interval: {
                  values: ['daily', 'weekly', 'monthly'],
                  required: true,
                },
                day: {
                  values: ['monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday', 'sunday'],
                  sortSuggestions: false,
                },
                time: {
                  single: ['string'],
                  validateValue: async (
                    template: unknown,
                    path: string[],
                    value: unknown,
                  ): Promise<Validation | undefined> => {
                    if (typeof value != 'string') return
                    if (!/^([01]?[0-9]|2[0-3]):[0-5][0-9]$/.test(value))
                      return {
                        message: html`Time must be in the format HH:MM`,
                        severity: 'error',
                      }
                  },
                },
                timezone: {
                  values: timezones,
                },
              },
            },
            'target-branch': {
              description: 'The branch to create pull requests against',
              single: ['string'],
            },
            vendor: {
              description: 'Update vendored or cached dependencies',
              single: ['boolean'],
            },
            'versioning-strategy': {
              description: 'How to update dependencies',
              values: ['lockfile-only', 'auto', 'widen', 'increase', 'increase-if-necessary'],
            },
          },
        },
      },
    },
  }
}

export function getRootRule(): RootRule {
  const rule = fullRootRule()
  return rule
}
